import React from 'react'
import ReactDOM from 'react-dom'
import {Container, Button} from 'react-bootstrap'
import Carousel from 'react-bootstrap/Carousel'

const Skills = () => {    
    return (
        <>
            <div className="skills_section" id="skills">
              <div className="section_spacer"></div>
                <Container>
                    <p className="text-white about">Skills</p>                    
                    <Carousel fade>                    
                      <Carousel.Item className="carousel d-flex justify-content-center align-items-center text-center">   
                      <button type="button" class="btn btn-info p-3 m-3">HTML5</button>
                      <button type="button" class="btn btn-info p-3 m-3">CSS</button>
                      <button type="button" class="btn btn-info p-3 m-3">JS</button>
                      <button type="button" class="btn btn-info p-3 m-3">ReactJS</button>
                      <button type="button" class="btn btn-info p-3 m-3">NodeJS</button>
                      <button type="button" class="btn btn-info p-3 m-3">ExpressJS</button>
                      <button type="button" class="btn btn-info p-3 m-3">Bootstrap</button>
                      <button type="button" class="btn btn-info p-3 m-3">WordPress</button>
                      <button type="button" class="btn btn-info p-3 m-3">MongoDB</button>                                          
                        <Carousel.Caption>
                          <h3>Web Development</h3>
                          <p></p>
                        </Carousel.Caption>
                      </Carousel.Item>                      
                      <Carousel.Item className="carousel d-flex justify-content-center align-items-center text-center">   
                      <button type="button" class="btn btn-info p-3 m-3">C</button><br/>
                      <button type="button" class="btn btn-info p-3 m-3">C++</button><br/>
                      <button type="button" class="btn btn-info p-3 m-3">Python</button><br/>
                      <button type="button" class="btn btn-info p-3 m-3">GitHub</button>                  
                        <Carousel.Caption>
                          <h3>Programming Languages</h3>
                          <p></p>
                        </Carousel.Caption>
                      </Carousel.Item>
                      <Carousel.Item className="carousel d-flex flex-wrap justify-content-center align-items-center text-center">   
                      <button type="button" class="btn btn-info p-3 m-2">Communication</button><br/>
                      <button type="button" class="btn btn-info p-3 m-2">Teamwork</button><br/>
                      <button type="button" class="btn btn-info p-3 m-2">Adaptability</button><br/>
                      <button type="button" class="btn btn-info p-3 m-2">Problem Solving</button><br/>
                      <button type="button" class="btn btn-info p-3 m-2">Creativity</button><br/>                      
                      <button type="button" class="btn btn-info p-3 m-2">Time Management</button><br/>
                      <button type="button" class="btn btn-info p-3 m-2">Leadership</button><br/>                      
                      <button type="button" class="btn btn-info p-3 m-2">Work Ethic</button>                  
                        <Carousel.Caption>
                          <h3>Soft Skills</h3>
                          <p></p>
                        </Carousel.Caption>
                      </Carousel.Item>
                    </Carousel>
                    </Container>                  
            </div>
        </>
    )    
}


export default Skills
