import React from 'react'
import { Container } from 'react-bootstrap'
import ReactDOM from "react-dom"

const Work = () => {
    return (
        <>
            <div className="work_section" id="work">
              <div className="section_spacer"></div>
              <Container>
                <p className="text-white about">Certifications and Projects</p>
                <div class="card text-white bg-dark mb-3" styles="max-width: 20rem;">
                  <div class="card-header"><a class="text-white" href="https://drive.google.com/drive/folders/1iaa01E5QcyTBnJWUhVQyizpSZi82-GFz?usp=sharing" target="_blank">Click here to view all the certifications</a></div>
                  <div class="card-body">
                    <h4 class="card-title">Certifications</h4>
                    <p class="card-text">I have completed various certifications and courses on Coursera, Udemy, Cisco, Oracle and NPTEL platforms.</p>
                  </div>
                </div>
                <div class="card text-white bg-dark mb-3" styles="max-width: 20rem;">
                  <div class="card-header"><a class="text-white" href="https://github.com/PremVispute" target="_blank">Click here to view all the projects</a></div>
                  <div class="card-body">
                    <h4 class="card-title">Projects</h4>
                    <p class="card-text">Feel free to check out some of my full stack development projects and internship work on my GitHub repository.</p>
                  </div>
                </div>
              </Container>
            </div>
        </>
    )
}

export default Work
