import React from 'react'
import ReactDOM from "react-dom"
import { Container } from 'react-bootstrap'

const About = () => {
    return (
        <>
            <div className="about_section" id="about">
            <div className="section_spacer"></div>
                <Container>
                    <p className="text-white about">About Me</p>
                    <div className="section_spacer"></div>
                    <p className="text-white about_text">Hi! I am Prem Vispute. Welcome to my portfolio website. </p>
                    <p className="text-white about_text">I am a final year undergraduate student pursuing my Bachelors in Information Technology. I love to work on difficult problems and coming up with elegant solutions to real world problems with the use of software and working with like-minded people. During my free time, I love to participate in competitive programming competitions and hackathons. </p>
                    <p className="text-white about_text">Web Development is one of my strongest skills. I have worked on websites since high school, and now I am looking for a more professional approach towards it and an internship will provide that for me. I am sure that I'll be a very valuable asset for your organization, and I'll also learn more about WebDev throughout this process.</p>
                    <p className="text-white about_text">Living, learning, and leveling up one day at a time.</p>
                    <div className="section_spacer"></div>
                    <div className="section_spacer"></div>
                </Container> 
            </div> 
        </>
    )
}

export default About
