import React from 'react'
import ReactDOM from "react-dom"
import image from '../images/profile.jpg'
import { Navbar, Nav, Container } from 'react-bootstrap'

const Profile = () => {
    return (
        <>
        {/* <div className="filler"></div>  */}
        <div className="profile">        
            <Navbar bg="primary" variant="dark" expand="lg">
                <Container>
                    <Navbar.Brand href="#home">Prem Vispute</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ms-auto p-2">
                        <Nav.Link href="#home">Home</Nav.Link>
                        <Nav.Link href="#about">About</Nav.Link>
                        <Nav.Link href="#skills">Skills</Nav.Link>
                        <Nav.Link href="#work">Work</Nav.Link>
                        <Nav.Link href="#contact">Contact</Nav.Link>
                    </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>   
            <img className="profile_image" src={image} alt="Profile" />         
            <div className="name text-center">
                <h1>Prem Vispute</h1>
                <h3>Full Stack Web Developer</h3>
                <h3>Programmer</h3><br/>
                <a href="https://drive.google.com/file/d/19zxQHkgMJLoSfJuR-htIGR2mk03Aeqpo/view?usp=sharing" target="_blank"> <button type="button" class="btn btn-dark">My Resume</button></a>
            </div>
            
        </div>        
        </>
    )
}

export default Profile
